import React, { Component } from 'react';
import Hello from './test/hello'

class App extends Component {
    constructor(props) {
      super(props);
      this.state = { liked: false };
    }
  
    render() {
      if (this.state.liked) {
        return <Hello></Hello>
      }
  
      return <label style={{color: 'red'}}
                    onClick={() => this.setState({ liked: true })}>Like a</label>
    }
  }

  export default App